@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
.header {
  height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 30px;
  -webkit-filter: invert(0);
          filter: invert(0);
  /* background-color: black; */
  position: -webkit-sticky;
  position: sticky;
  color: white;
  top: 0;
  mix-blend-mode: difference;
}

.left-text {
  width: 50vw;
  padding-left: 1vw;
  text-align: left;
}

.right-text {
  width: 50vw;
  padding-right: 1vw;
  text-align: right;
}

.link {
  background-color: black;
  color: white;
  border: none;
  text-decoration: none;
  font-size: 40px;
  margin-left: 10px;
}

.content-box {
  height: 90vh;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.content-left {
  width: 40vw;
  height: 60vh;
  margin-right: auto;
  margin-left: auto;
}

.content-right {
  width: 40vw;
  height: 60vh;
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.right-title {
  display: -webkit-flex;
  display: flex;
  height: 10vh;
  background-color: black;
  font-size: 30px;
  color: white;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.right-output {
  height: 50vh;
  white-space: pre-wrap;
  font-family: monospace;
  color: white;
  padding: 10px;
}

.left-title {
  display: -webkit-flex;
  display: flex;
  height: 10vh;
  background-color: black;
  font-size: 30px;
  color: white;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.left-input {
  height: 50vh;
  color: white;
  border-radius: 30px;
}

.left-input-area {
  width: 96%;
  height: 86%;
  box-sizing: content-box;
  padding: 2%;
  background-color: #272822;
  color: white;
  border: none;
  resize: none;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.left-input-area:focus {
  outline: none;
}

.run-button {
  background-color: black;
  color: white;
  border: none;
  font-size: 20px;
  margin-left: 10px;
}

.left-text-box-header {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background-color: #272822;
}
.circle {
  font-size: 12px;
  margin-left: 4px;
}

.circle-red {
  margin-left: 8px;
  color: #ee535b;
}

.circle-yellow {
  color: #fcc130;
}

.circle-green {
  color: #37d440;
}

.background-changing-div {
  background: url(/SQL-Editor/static/media/background.c8116fb7.svg) no-repeat;
  height: 40vw;
}

.page-title {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-size: 40px;
}

.content1 {
  height: 100vh;
}

.left-help {
  width: 27vw;
  margin-left: auto;
  margin-right: auto;
}
.right-help {
  width: 27vw;
  margin-left: auto;
  margin-right: auto;
}

.help-div {
  display: -webkit-flex;
  display: flex;
  height: 55vh;
  margin-top: 10vh;
  margin-left: auto;
  margin-right: auto;
}

.title {
  text-align: center;
  font-size: 30px;
}

.point {
  margin-top: 3vh;
}

.top {
  margin-top: 5vh;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: "Noto Sans JP", sans-serif;
}

.App {
  background-color: white;
}

body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-thumb {
  background: #fff;
}

body::-webkit-scrollbar-track {
  background: black;
}

